

.i{
    display:flex;
    height: 100vh;

}

.i-left{
    flex: 1;
    display: flex;
    align-items:center;
    justify-content: center;
}

.i-right{
    flex: 1;
    position: relative;
}

.i-left-wrapper{
    padding:50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-intro1{
    font-size: 25px;
    font-weight:200;
    font-family: 'Poppins', sans-serif;
    color: rgba(248, 248, 248, 0.849);

}

.i-intro{
    font-size: 30px;
    font-weight:300;
     /* font-family: myfont;  */
    /* color: azure; */
    color: rgba(51, 51, 51, 0.822);

}

.i-name{
    font-size: 60px;
}

.i-title{
    height: 50px;
    overflow:hidden;
    

}

.i-title-wrapper{
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
    25%{
        transform: translateY(-50px);
    }
    50%{
        transform: translateY(-100px);
    }
    75%{
        transform: translateY(-150px);
    }
    100%{
        transform: translateY(-200px);
    }
   
}

.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #000000e5;
    display: flex;
    align-items: center;
    background-color:rgba(253, 248, 201, 0.356);
    padding:0.009em;
    
}

.i-scroll{
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
}

.i-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
}



.i-desc{
    color:lavender;
    font-size:18px;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

